//main: ../main.scss

/**
* Basic typography style for copy text
*/
body {
    color: $text-color;
    font: normal 125% / 1.4 $text-font-stack;
}
  
p {
    font-size: 15px;
    line-height: 1.6;

    @media (max-width: 991px) {
        font-size: 14px;
    }
}

i,
em {
    font-style: italic;
}
  
.heading {
    position: relative;
    margin: 0 0 32px;
    width: fit-content;
    font-family: $font-gotham-bold;
    font-weight: 700;
    line-height: 1;
    font-size: 45px;
    color: #fff;
    text-transform: uppercase;
    text-shadow: 0 2px 4px #000;

    &-center {
        margin-left: auto;
        margin-right: auto;
    }

    &-small {
        font-size: 38px;
    }

    &.dashed {
        padding-bottom: 20px;
        margin-bottom: 55px;

        &:after {
            content: "";
            position: absolute;
            bottom: -5px;
            left: 50%;
            z-index: 2;
            width: 100%;
            border-bottom: 2px dashed #ccc;
            transform: translateX(-50%);
        }
    }

    &.no-margin {
        margin-bottom: 0;
    }

    @media (max-width: 1199px) {
        font-size: 34px;

        &-small {
            font-size: 30px;
        }
    }

    @media (max-width: 991px) {
        font-size: 28px;
        line-height: 1.2;

        &-small {
            font-size: 24px;
        }
    }
}
  
.subheading {
    margin: 0 0 32px;
    font-family: $font-libre-baskerville-italic;
    font-weight: normal;
    line-height: 1.4;
    font-size: 28px;
    text-shadow: 0 2px 4px #000;
  
    &-small {
        font-size: 24px;
    }
  
    @media (max-width: 1199px) {
        font-size: 22px;

        &-small {
            font-size: 20px;
        }
    }
  
    @media (max-width: 991px) {
        font-size: 18px;
        line-height: 1.6;

        &-small {
            font-size: 16px;
        }
    }
}

.text-shadow {
    &-white {
        text-shadow: 0 0 5px #fff !important;
    }

    &-black {
        text-shadow: 0 0 5px #000 !important;
    }
}
